.App {
  text-align: center;
}

.container {
  max-width: 600px;
  margin: auto;
  padding: 20px 40px;
}

.centered-container {
  max-width: 420px;
  margin: 30px auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.sketch-picker {
  min-width: 200px;
  max-width: 400px;
  width: 100% !important;
}

.chrome-picker {
  width: 100% !important;
}

.btn {
  background-color: #fff;
  border: solid #eee;
  border-width: 0 0 4px 0;
  color: #333;
  padding: 12px 24px;
  font-size: 18px;
  border-radius: 4px;
  display: block;
  transition: all .4s ease;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.2);
}

.btn.btn-rainbow {
  margin: 10px 0;
}

.btn.btn-nav {
  margin: 10px 0;
  width: 100%;
}

.btn.btn-rainbow:hover {
  background-color: #f83647;
  border-color: #b62534;
  color: #fff;
}

.settings {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0 5px;
  width: 100%;
}

a.link-btn {
  text-decoration: none;
  width: 100%;
}

.grid {
  display: grid;
  padding: 0 20px;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

.grid > div {
  text-align: center;
}

.picker-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.actions {
  margin: 20px;
}

.btn-success {
  background-color: #4fc639;
  border-color: #44a62f;
  color: #fff;
}

.btn-gradient {
  border: none;
  color: #fff;
}

.pattern-container {
  margin: 40px 0;
}
