input[type=range] {
  -webkit-appearance: none;
  appearance: none;
  width: 90%;
  height: 25px;
  border-radius: 25px;
  padding: 3px;
  background: grey;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity, background-color .2s;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #fff;
  cursor: pointer;
}

input[type=range]::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 25px;
  background: #fff;
  cursor: pointer;
}
